<style media="screen">
    .origin_country .dropdown-toggle{
        border-top-left-radius:0px !important;
        border-bottom-left-radius:0px !important;
    }
    .add-product-hide-input span.msg-box{
        position: absolute;
        z-index: 999;
    }
    #add-product-from span.n-right{
        top: 3px;
        right: 0px;
    }
    .edit-product-title{
        font-size: 0px;
    }
    .edit-product-title li{
        font-size: 14px;
        width: 50%;
    }
</style>
<template>
    <div class="col-md-12 addproduct-box-html form-horizontal">
        <div class="well well-sm">
            <ul class="edit-product-title list-inline">
                <li></li>
                <li class="text-right"><button @click="modification" type="button" class="btn btn-danger"></button></li>
            </ul>
        </div>
        <div class="form-group">
            <label class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
                <p class="control-label text-muted">
                    1 > 2 > 3
                </p>
            </div>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
            <div class="col-sm-4">
                <input v-model="info.productName" name="productName" maxlength="30" type="text" class="form-control" placeholder="">
            </div>
            <span class="col-sm-4 control-label">
                <div class="text-left text-muted">{{info.productName.length}}/30</div>
            </span>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
            <div class="col-sm-4">
                <input v-model="info.sellingPoint" name="sellingPoint" type="text" class="form-control" placeholder="">
            </div>
            <span class="col-sm-4 control-label">
                <div class="text-left text-muted">{{info.sellingPoint.length}}/30</div>
            </span>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"></label>
            <div class="col-sm-3">
                <input v-model="info.productModel" name="productModel" type="text" class="form-control" placeholder="">
            </div>
        </div>

        <!--  -->
        <brand :brandid.sync="info.brandId" :brandlist="brandList"></brand>

        <!--  -->
        <designers :designersid.sync="info.designerVoList"></designers>

        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span>/</label>
            <div class="col-sm-4">
                <div class="input-group add-product-hide-input">

                </div>
            </div>
        </div>

        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span>/-</label>
            <div class="col-sm-4">
                <div class="input-group add-product-hide-input">

                </div>
            </div>
            <div class="col-sm-4">
                <div class="input-group add-product-hide-input">

                </div>
            </div>
        </div>

        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
            <div class="col-sm-2 add-product-hide-input">
                <input data-rule="required" name="styleId" v-bind:value="info.styleId"  type="text" class="form-control" placeholder="">
                <select v-model="info.styleId" class="form-control">
                    <option v-for="item in style" v-bind:value="item.keyId">
                        {{ item.keyValue }}
                    </option>
                 </select>
            </div>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
            <div class="col-sm-2">
                <div class="input-group">
                    <span class="input-group-addon"></span>
                    <input v-model="info.length" name="length" type="number" class="form-control" placeholder="">
                    <span class="input-group-addon">cm</span>
                </div>
            </div>
            <div class="col-sm-2">
                <div class="input-group">
                    <span class="input-group-addon"></span>
                    <input v-model="info.width" name="width" type="number" class="form-control" placeholder="">
                    <span class="input-group-addon">cm</span>
                </div>
            </div>
            <div class="col-sm-2">
                <div class="input-group">
                    <span class="input-group-addon"></span>
                    <input v-model="info.height" name="height" type="number" class="form-control" placeholder="">
                    <span class="input-group-addon">cm</span>
                </div>
            </div>
            <span class="col-sm-4 control-label">
                <div class="text-left text-muted"></div>
            </span>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
            <div class="col-sm-2 add-product-hide-input">
                <input data-rule="required" name="applicablePeople" v-bind:value="info.applicablePeople" type="text" class="form-control" placeholder="">
                <select v-model="info.applicablePeople" class="form-control">
                    <option v-for="item in applicable_people" v-bind:value="item.keyId">
                        {{ item.keyValue }}
                    </option>
                 </select>
            </div>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
            <div class="col-sm-2 add-product-hide-input">
                <input data-rule="required" name="canCustomized" v-bind:value="info.canCustomized" type="text" class="form-control" placeholder="">
                <select v-model="info.canCustomized" class="form-control">
                    <option v-for="item in canCustomized" v-bind:value="item.value">
                        {{ item.text }}
                    </option>
                 </select>
            </div>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span>
            </label>
            <div class="col-sm-7 bg-muted">
                {{info.applicableSceneList}}
                <label v-for="item in applicable_scene" class="checkbox-inline">
                    <input data-rule="checked[1~]" type="checkbox" name="applicableScene[]" v-model="info.applicableSceneList" v-bind:value="item.keyId"> {{item.keyValue}}
                </label>
            </div>
        </div>
        <div class="form-group">
            <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
            <div class="col-sm-7 bg-muted">
                {{info.materialList}}
                <label v-for="item in materialList" class="checkbox-inline">
                    <input data-rule="checked[1~]" type="checkbox" name="applicableScene[]" v-model="info.materialList" v-bind:value="item.id"> {{item.name}}
                </label>
            </div>
        </div>
        <div class="form-group">
            <label class="col-sm-2 control-label"></label>
            <div class="col-sm-7">
                <p class="control-label text-muted">
                    <textarea name="detailDescription" placeholder="" class="form-control" rows="5">{{info.detailDescription}}</textarea>
                </p>
            </div>
            <span class="col-sm-3 control-label">
                <div class="text-left text-muted">{{info.detailDescription.length}}/200</div>
            </span>
        </div>
    </div>
</template>
<script type="text/javascript">
    import brand                from    './brand'
    import designers            from    './Designers.vue'
    import {API_ROOT}           from    '../../../../config'

    export default{
        props:['info'],
        components:{
            brand,
            designers
        },
        data(){
            return{
                canCustomized:[
                    {text:'',value:true},{text:'',value:false}
                ],
                style:[],
                applicable_people:[],
                applicable_scene:[],
                materialList:[],
                brandList:[],
            }
        },
        watch:{
            'info':function(val,oldval){
                let _this=this;

                //
                this.httpGet('v1/material/queryCategoryMaterial',{"categoryId":val.categoryId},'',function(data){
                    _this.$set('materialList',data.materialList)
                });

                //
                this.httpGet('v1/brand/queryAll',{},'',function(data) {
                    _this.$set('brandList',data.data.brandList)
                });

                //
                this.$broadcast('designerslist', 'test');
            }
        },
        compiled(){
            let _this=this;

            //
            this.httpGet('v1/dictionarys',{"keyGroups":["style","applicable_people","applicable_scene"]},'',function(data) {
                _this.$set('style',data.data.style)
                _this.$set('applicable_people',data.data.applicable_people)
                _this.$set('applicable_scene',data.data.applicable_scene)
            });

        },
        methods:{
            /**
             * 
             * 1designersiddesignerName
             */
            modification:function(){
                console.log(this.info.brandId);
                console.log(this.info.width);
                console.log(this.info.designerVoList);
            },

            /**
             * http
             * callback
             * urlurldataerrText
             */
            httpGet:function(url,data,errText,callback){
                let jsontext=JSON.stringify(data);
                this.$http.get(API_ROOT+url,{paramJson:jsontext}).then((response) => {
                    if (response.data.resCode==0) {
                        callback(response.data);
                    }else {
                    }
                }, (response) => {
                });
            }
        },
        ready(){

        }
    }
</script>



