<template>

    <div class="form-group">
        <label for="firstname" class="col-sm-2 control-label"><span class="text-danger">*</span></label>
        <div class="col-sm-3 add-product-hide-input">
            <input v-model="brandid" type="text" class="form-control" placeholder="id">
            <v-select :value.sync="selected" :on-change="setSelect" label="brandCname" :debounce="500" placeholder="" :options="brandlist"></v-select>
        </div>
    </div>
</template>
<script type="text/javascript">
    import vSelect from '../../../../components/common/vue-select/src/index.js'
    export default{
        props:['brandlist','brandid'],
        components:{
            vSelect
        },
        data(){
            return{
                selected:{},
            }
        },
        methods:{
            setSelect:function(val){
                /**
                 * id
                 */
                if (val==null) {
                    this.$set('brandid','')
                }else {
                    this.$set('brandid',val.id)
                }
            }
        },
        watch:{
            'brandlist':function(){
                /**
                 * id
                 * idlistid
                 * find()
                 */
                let findBrand = (value,index,array) => {
                    if (value.id==this.brandid) {
                        return value;
                    }
                };
                this.$set('selected',this.brandlist.find(findBrand))
            }
        }
    }
</script>



